import * as Yup from "yup";

export const registrationValidation = Yup.object().shape({
  email: Yup.string()
    .required("*   ")
    .email("*   "),
  password: Yup.string()
    .required("*   ")
    .min(6, "* -  6")
    .max(40, "* -  40"),
  confirmPassword: Yup.string()
    .required("*   ")
    .oneOf([Yup.ref("password")], "*  "),
});
